@keyframes a-grow {
  to {
    width: 100%;
  }
}

@keyframes a-appearance {
  to {
    opacity: 1;
  }
}

@keyframes a-appearance-reverse {
  to {
    opacity: 0;
  }
}

@keyframes a-scale-x {
  to {
    transform: scaleX(1);
  }
}

@keyframes a-texts-appearance {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes a-svg-text-appearance {
  0% {
    stroke-dashoffset: 800;
  }

  100% {
    stroke-dashoffset: 1;
    opacity: 1;
  }
}

@keyframes a-suitecase-grow {
  from {
    transform: translate(-50%, 50%) scale(0.1);
    opacity: 0;
  }

  to {
    transform: translate(-60%, -15%) scale(1);
    opacity: 1;
  }
}

@keyframes a-flamingo-appearance {
  from {
    transform: translate(-50%, 50%) scale(0.1);
    opacity: 0;
  }

  to {
    transform: translate(-100%, 10%) scale(1);
    opacity: 1;
  }
}

@keyframes a-flamingo-move {
  0% {
    transform: translate(-100%, 10%) rotate(0);
  }

  50% {
    transform: translate(-97%, 5%) rotate(5deg) skewY(2deg);
  }

  100% {
    transform: translate(-100%, 10%) rotate(0);
  }
}

@keyframes a-leaf-green-move {
  0% {
    transform: translate(-2%, 40%) rotate(0deg);
    opacity: 0;
  }

  25% {
    transform: translate(-2%, 40%) rotate(8deg) skewY(5deg);
    opacity: 1;
    z-index: 1;
  }

  40% {
    transform: translate(-2%, 40%) rotate(4deg);
    opacity: 1;
    z-index: 1;
  }

  65% {
    transform: translate(-2%, 40%) rotate(6deg) skewY(2deg);
    opacity: 1;
    z-index: 1;
  }

  80% {
    transform: translate(-2%, 40%) rotate(5deg);
    opacity: 1;
    z-index: 1;
  }

  100% {
    transform: translate(-2%, 40%) rotate(6deg);
    opacity: 1;
    z-index: 1;
  }
}

@keyframes a-leaf-blue-first {
  0% {
    transform: translate(5%, -40%) rotate(0deg);
    opacity: 0;
  }

  25% {
    transform: translate(5%, -25%) rotate(10deg) skewY(5deg);
    opacity: 1;
  }

  40% {
    transform: translate(5%, -30%) rotate(4deg);
    opacity: 1;
  }

  65% {
    transform: translate(5%, -25%) rotate(10deg) skewY(2deg);
    opacity: 1;
  }

  85% {
    transform: translate(5%, -30%) rotate(6deg);
    opacity: 1;
  }

  100% {
    transform: translate(5%, -30%) rotate(10deg);
    opacity: 1;
  }
}

@keyframes a-leaf-blue-second {
  0% {
    transform: translate(-130%, 0%) rotate(5deg);
    opacity: 0;
  }

  25% {
    transform: translate(-130%, 15%) rotate(-10deg) skewY(-5deg);
    opacity: 1;
  }

  40% {
    transform: translate(-130%, 10%) rotate(-4deg);
    opacity: 1;
  }

  65% {
    transform: translate(-130%, 15%) rotate(-10deg) skewY(-2deg);
    opacity: 1;
  }

  85% {
    transform: translate(-130%, 10%) rotate(-6deg);
    opacity: 1;
  }

  100% {
    transform: translate(-130%, 10%) rotate(-10deg);
    opacity: 1;
  }
}

@keyframes a-control-prev {
  0% {
    transform: translateX(-100px) rotate(-90deg);
    opacity: 0;
  }

  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}

@keyframes a-control-next {
  0% {
    transform: translateX(100px) rotate(90deg);
    opacity: 0;
  }

  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}

@keyframes a-result-title-svg {
	0% {
		transform: translateY(0%);
	}

	50% {
		transform: translateY(50px);
	}

	75% {
		transform: translateY(43px);
	}

	100% {
		transform: translateY(50px);
	}
}

@keyframes a-control-again {
  0% {
    transform: translateX(100px) rotate(90deg);
    opacity: 0;
  }

  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}

@keyframes a-award-to-rotate {
  to {
    transform: translate(20px, -250px) rotate(40deg) scale3d(0.3, 0.3, 0.3);
  }
}

@keyframes a-crooked-motion {
  10% {
    transition-timing-function: ease-in-out;
    transform: translate(-10px, -255px) rotate(-20deg) scale3d(0.31, 0.31, 0.31);
  }

  20% {
    transition-timing-function: linear;
    transform: translate(-120px, -220px) rotate(-20deg) scale3d(0.35, 0.35, 0.35);
  }

  40% {
    transition-timing-function: linear;
    transform: translate(-120px, -200px) rotate(20deg) scale3d(0.5, 0.5, 0.5);
  }

  80% {
    transition-timing-function: ease-out;
    transform: translate(0, -50px) rotate(-10deg) scale3d(0.9, 0.9, 0.9);
  }

  100% {
    transition-timing-function: ease-in;
    transform: translate(0, 0) rotate(0) scale3d(1, 1, 1);
  }
}
